:root {
    --background_page: rgb(245, 245, 245);
    --header_background: linear-gradient(0deg, #9738A2, #B543C2);
}

.Header
{ 
    position: relative;
    background-color:#9738A2;
    border-bottom-left-radius: 50px;
    padding-bottom: 15px;
    z-index: 100;
}

.CatItem
{
    width:110px;
    height: 130px;
    background: #fff;
    border-radius: 22px;
    margin-left:15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .25);
}

.InfoHeader
{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:#fff;
    margin-bottom: 40px;
}

.InfoUser
{
    float:left;
    margin-left: 5px;
}
.InfoNotice
{
    float: right;
}

.CatItemText
{
    font-weight: 600;
    font-size: 14px;
}

.CatItemImg
{
    width: 61px;
    height: 61px;

    margin-top: 15px;
    margin-bottom: 30px;
    border-radius: 50px;
}

.HeaderImg
{
    width: 40px;
    height: 40px;

    border-radius: 50px;
}

.InfoImg
{
    margin-left: 10px;
    float:right;
}

.TextBold
{
    font-weight: 600;
    font-size: 16px;
}

.TextSmall
{
    font-size: 13px;
}

.ActionHeader
{
    display: flex;
    align-items: flex-end;

    padding-left: 20px;
    position: relative;
    background: #eeeeee;
    border-bottom-left-radius: 50px;
    margin-top: -50px;
    height: 75px;
    z-index: 99;
}

.ActionItem
{
    padding: 5px;
    background: #AE97D6;
    color: #fff;
    text-align: center;
    border-radius: 50px;
    padding-left: 15px;
    padding-right: 15px;

    margin-left: 10px;

    font-size:14px;
}

.BlockName
{
    margin-top: 10px;
}

.TitleBlock
{
    font-weight: 600;
    font-size:20px;
}

.AboutBlock
{
    margin-top: 1px;
    font-size:14px;
    color: #B9B9B9;
}

.BlockItem
{
    background: #fff;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, .35);

    height: 110px;

    border-radius: 25px;

    overflow: hidden;

    margin-bottom: 15px;
}

.BlockImage
{
    display: block;
    float:left;

    height: 100%;
    width: 20%;
    
    margin-top: 25px;
    margin-left: 20px;
}

.BlockItemImage 
{
    width: 60px;
    height: 60px;

    border-radius: 50px;
}

.BlockInfo
{
    display:block;

    height: 100%;
    width: 100%;

    margin-top: 15px;
    margin-left: 10px;
}

.BlockTitle
{
    font-weight: 600;
    font-size: 16px;
}

.BlockCategory
{
    margin-top: 3px;

    color: #BEBEBE;
    font-size: 14px;

}

.BlockDescp
{
    margin-top: 5px;
    width: 100%;
    font-size: 14px;
}

.BlockFavorite
{
    float: right;
    margin: 20px;
}
.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

