
.BlockItem
{
    background: #fff;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, .35);

    height: 110px;

    border-radius: 25px;

    overflow: hidden;

    margin-bottom: 15px;
}

.BlockImage
{
    display: block;
    float:left;

    height: 100%;
    width: 20%;
    
    margin-top: 25px;
    margin-left: 20px;
}

.BlockItemImage 
{
    width: 60px;
    height: 60px;

    border-radius: 50px;
}

.BlockInfo
{
    display:block;

    height: 100%;
    width: 100%;

    margin-top: 15px;
    margin-left: 10px;
}

.BlockTitle
{
    font-weight: 600;
    font-size: 16px;
}

.BlockCategory
{
    margin-top: 3px;

    color: #BEBEBE;
    font-size: 14px;

}

.BlockDescp
{
    margin-top: 5px;
    width: 100%;
    font-size: 14px;
}

.BlockFavorite
{
    float: right;
    margin: 20px;
}